import { Http, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ModalController, NavParams } from 'ionic-angular';

import 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';

@Injectable()

export class updateService {
	public updateDetails = '';

	constructor(public http: Http, public modalCtrl: ModalController) {}

	updateNow(event) {
	    //console.log(event);   
	    this.updateDetails = 'email changed';

		const body = event;

		//post to database
		var headers = new Headers();
		headers.append('Content-Type', 'application/x-www-form-urlencoded; charset=UTF-8');

		//console.log(body);

		//http://byhomeowner.000webhostapp.com/ionic_test/update_data.php
		//http://192.168.192.113/preetam/ionic/myIonicApp/src/assets/update_data.php

		return this.http.post('http://byhomeowner.000webhostapp.com/ionic_test/update_data.php', body, {
		 	headers: headers
		})
		.map(res => res.text());
    
  	}


}


